import Vue from 'vue'
import Router from 'vue-router'
import routes from './routers'
import store from '@/store'
import iView from 'view-design'
import { setToken, getToken, setTitle } from '@/libs/util'
import config from '@/config'
const { homeName } = config

Vue.use(Router)

const LOGIN_PAGE_NAME = 'login'

// const turnTo = (to, access, next) => {
//   if (canTurnTo(to.name, access, routes)) next() // 
//   else next({ replace: true, name: 'error_401' }) // 401
// }

const createRouter = () =>
    new Router({
        mode: 'hash',
        routes: routes
    })

const beforeEachHandler = (to, from, next) => {
    iView.LoadingBar.start()

    if (to.name === 'error_404' && !store.state.loaded) {
        const ctrl = setInterval(() => {
            if (store.state.loaded) {
                clearInterval(ctrl)
                router.push(to.path)
            }
        }, 50)
        return
    }

    const token = getToken()
    if (!token && to.name !== LOGIN_PAGE_NAME) {
    // 
        next({
            name: LOGIN_PAGE_NAME // 
        })
    } else if (!token && to.name === LOGIN_PAGE_NAME) {
    // 
        next() // 
    } else if (token && to.name === LOGIN_PAGE_NAME) {
    // 
        next({
            name: homeName // homeName
        })
    } else {
        if (store.state.user.hasGetInfo) {
            // turnTo(to, store.state.user.access, next)
            next()
        } else {
            store
                .dispatch('getUserInfo')
                .then((user) => {
                    console.log(' ~ file: index.js ~ line 60 ~ .then ~ user', user)
                    // name;access['super_admin'] ['super_admin', 'admin']
                    // turnTo(to, user.access, next)
                    next()
                })
                .catch(() => {
                    setToken('')
                    next({
                        name: 'login'
                    })
                })
        }
    }
}

const afterEachHandler = (to) => {
    setTitle(to, router.app)
    iView.LoadingBar.finish()
    window.scrollTo(0, 0)
}

const router = createRouter()

router.beforeEach(beforeEachHandler)
router.afterEach(afterEachHandler)

export const resetRouter = () => {
    const newRouter = createRouter()
    newRouter.beforeEach(beforeEachHandler)
    newRouter.afterEach(afterEachHandler)

    router.matcher = newRouter.matcher // the relevant part
}

export default router
